$p: #7f828b;

*{
    padding: 0;
    margin: 0;
    font-family: 'Roboto Condensed', sans-serif;
}

.App{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 100vh;
}

.backing{
    position: absolute;
    background-color: $p;
    height: 65vh;
    width: 100vw;
    border-radius: 0 0 50% 0;
    z-index: 0;
    top: 0;
}

.title{
    color: white;
    z-index: 2;
    align-self: baseline;
    margin-left: 30px;
    padding-top: 20px;
}
.choice{
    height: 65vh;
    width: 87vw;
    background-color: white;
    z-index: 999;
    overflow: hidden;
    max-width:100%;
    max-height:100%;
    position: relative;
    border-radius: 7px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.options{
    width: 50%;
    display: flex;
    justify-content: space-between;
    font-size: 50px; 
}

.footer{
    background-color: rgb(245, 240, 240);
    position: absolute;
    color: black;
    bottom: 0;
    height: 70px;
    width: 100%;
    border-radius: 15px 15px 0% 0%;
    padding-left: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    font-size: 20px;
}

.yes{
    color: red;
}

.no{
    color: black;
}

.option_box{
    height: 80px;
    width: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}